import React, { Component } from 'react';
import './index.scss';
import { fromJS, is } from 'immutable';
import { connect } from 'react-redux';
import Swiper from "swiper/dist/js/swiper.js";

class XButton extends Component {
	state = {
		list: [1,2,3],
		count: 0
	}

	componentDidMount() {
		this.swiper = new Swiper(this.swiperDom, {
			pagination: {
				el: '.swiper-pagination',
				clickable: true, 
				renderBullet: function (index, className) {
					return '<span class="' + className + '">' + (index + 1) + '</span>';
				}
			},
			loop: true,
			autoplay: true,
			grabCursor: true,
			autoplay: {
				disableOnInteraction: false
			}
		})

		this.timer = setInterval(() => {
			this.setState(prevState => ({
				count: prevState.count + 1
			}))
		}, 1000)
	}

	shouldComponentUpdate(nextProps, nextState) {
		return !is(fromJS(this.props), fromJS(nextProps)) || !is(fromJS(this.state), fromJS(nextState))
	}

	componentWillUnmount() {
		clearInterval(this.timer)
	}

	onMouseEnter = (e) => {
		this.swiper.autoplay.stop()
	}

	onMouseLeave = (e) => {
		this.swiper.autoplay.start()
	}

	render() {
		return (
			<div id="x-swiper">
				<div>{this.state.count}</div>
				<div className="swiper-container" ref={e=> this.swiperDom = e} 
					onMouseEnter={this.onMouseEnter} onMouseLeave={this.onMouseLeave}>
					<div className="swiper-wrapper">
						<div className="swiper-slide grey">slider1</div>
						<div className="swiper-slide blue">slider2</div>
						<div className="swiper-slide red">slider3</div>
					</div>
					<div className="swiper-pagination"></div>
				</div>
			</div>
		);
	}
}

export default connect(state => ({
	configs: state.configs
}), {

})(XButton)
